// Dependencies
import React, { useState } from "react";

// Styles
import "./tailwind.output.css";

const App = () => {
  const [count, setCount] = useState(0);
  const decreaseHandler = () => {
    if (count > 0) {
      setCount(count - 1);
    }
  };
  const increaseHandler = () => {
    setCount(count + 1);
  };
  const resetHandler = () => {
    setCount(0);
  };

  return (
    <div className="w-screen h-screen flex justify-center bg-blue-500 flex-col gap-5">
      <h2 className="text-center text-2xl font-bold text-blue-900">
        Counter App
      </h2>
      <div className="bg-white flex mx-auto rounded px-3 py-1 gap-10 text-[25px] w-26">
        <button onClick={decreaseHandler} className="border-r-2 px-3 text-3xl">
          -
        </button>
        <div className="text-3xl">{count}</div>
        <button onClick={increaseHandler} className="border-l-2 px-3 text-3xl">
          +
        </button>
      </div>
      <button
        onClick={resetHandler}
        className="text-2xl bg-blue-300 flex justify-center w-20 mx-auto"
      >
        Reset
      </button>
    </div>
  );
};

export default App;
